import * as React from 'react'
import { useEffect, useState } from 'react'
import {
  CREATE,
  CreateParams,
  sanitizeFetchType,
  UPDATE_MANY,
  UpdateManyParams,
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
} from 'react-admin'
import { Button, createStyles, makeStyles, Theme } from '@material-ui/core'
import {
  Access,
  AccessToggleButtonStateEntry,
} from '../../core/ticket-details/access-button.state'
import { AccessUsingsCreateParams } from '../../core/access-usings/access-usings-create.params'
import { TicketDetails } from '../../core/ticket-details/ticket-details'
import { ResourceName } from '../../core/ResourceName'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginLeft: theme.spacing(1),
    },
  }),
)

type AccessButtonStateEntry =
  AccessToggleButtonStateEntry<AccessUsingsCreateParams>

const accessButtonState: Record<Access, AccessButtonStateEntry> = {
  [Access.BLOCK]: [
    Access.BLOCK,
    {
      text: 'Oznacz dostęp jako wykorzystany',
    },
  ],
  [Access.UNBLOCK]: [
    Access.UNBLOCK,
    {
      text: 'Zwolnij dostęp',
    },
  ],
  [Access.DISABLED]: [
    Access.DISABLED,
    {
      text: 'Oznacz dostęp jako wykorzystany (N/A)',
    },
  ],
}

export const BlockAccessButton = () => {
  const classes = useStyles()
  const { loading } = useShowContext()
  const notify = useNotify()
  const { tickets, accessesIds } = useRecordContext<TicketDetails>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const [[access, buttonState], setButtonStateEntry] =
    useState<AccessButtonStateEntry>(accessButtonState.DISABLED)
  const refresh = useRefresh()

  useEffect(() => {
    if (tickets.some((t) => t.accessUsings.some((a) => a.active))) {
      setButtonStateEntry(accessButtonState.UNBLOCK)
    } else {
      setButtonStateEntry(accessButtonState.BLOCK)
    }
  }, [tickets])

  const onClick = async () => {
    try {
      if (access === Access.BLOCK) {
        const ticketIds = tickets.map((t) => t.id)
        await mutate(
          {
            type: sanitizeFetchType(CREATE),
            resource: ResourceName.ACCESS_USINGS,
            payload: {
              data: { ticketIds, accessIds: accessesIds, verifierDeviceId: 1 },
            } as CreateParams<AccessUsingsCreateParams>,
          },
          { returnPromise: true },
        )
      } else {
        const activeAccessUsingIds = tickets
          .flatMap((t) => t.accessUsings)
          .filter((au) => au.active)
          .map((au) => au.id)
        await mutate(
          {
            type: sanitizeFetchType(UPDATE_MANY),
            resource: ResourceName.ACCESS_USINGS,
            payload: {
              ids: activeAccessUsingIds,
            } as UpdateManyParams,
          },
          { returnPromise: true },
        )
      }
      refresh()
    } catch (err) {
      notify(err?.message, 'error')
    }
  }

  return (
    <Button
      disabled={access === Access.DISABLED || loading || mutationLoading}
      className={classes.button}
      variant="contained"
      color="secondary"
      onClick={onClick}
    >
      {buttonState.text}
    </Button>
  )
}
