import React from 'react'
import ReceiptIcon from '@material-ui/icons/Receipt'
import { Admin, Resource } from 'react-admin'
import { Dashboard } from './main/Dashboard'
import { customTheme } from './custom.theme'
import { providersFactory } from './core/providers.factory'
import { TicketInfoList } from './main/ticket-info/TicketInfoList'
import { TicketDetailsView } from './main/ticket-details/TicketDetailsView'
import { ResourceName } from './core/ResourceName'
import { LogList } from './main/logs/LogList'

// TODO: merge customers and products view
// TODO: chage dashboard name - https://stackoverflow.com/questions/59879156/react-admin-how-to-change-the-dashboard-name-in-the-menu/59880647#59880647
// TODO: create browser router - https://marmelab.com/react-admin/Admin.html#history
// TODO: handle InfoDetailsView event for releasing access
// TODO: add fake providers
const App = () => {
  const { authProvider, dataProvider } = providersFactory(process.env.NODE_ENV)
  return (
    <Admin
      theme={customTheme}
      dashboard={Dashboard}
      authProvider={authProvider}
      dataProvider={dataProvider}
    >
      <Resource
        options={{ label: 'Bilety' }}
        icon={ReceiptIcon}
        name={ResourceName.TICKET_INFO}
        list={TicketInfoList}
      />
      <Resource
        options={{ label: 'Wejściówka' }}
        name={ResourceName.TICKET_DETAILS}
        show={TicketDetailsView}
      />
      <Resource
        options={{ label: 'Logi' }}
        name={ResourceName.LOGS}
        list={LogList}
      />
    </Admin>
  )
}

export default App
