import * as React from 'react'
import {
  Datagrid,
  List,
  ListProps,
  TextField,
  useListContext,
} from 'react-admin'
import { useLocation } from 'react-router-dom'
import { Box, Divider, Typography } from '@material-ui/core'
import { FindUserByTicket } from '../common/FindUserByTicket'
import { MaskField } from '../common/MaskField'
import { ResourceName } from '../../core/ResourceName'

const TicketInfoGrid = () => {
  const {
    loading,
    filterValues: { ticketCode },
  } = useListContext()
  return (
    <>
      <Box m={2}>
        <Typography variant="h5" paragraph>
          Wyszukiwanie biletu po kodzie: {ticketCode}
        </Typography>
      </Box>
      <Divider />
      <Datagrid
        loaded={!loading}
        rowClick={(id) => `${ResourceName.TICKET_DETAILS}/${id}/show`}
      >
        <TextField label="Imię" source="firstname" sortable={false} />
        <TextField label="Nazwisko" source="lastname" sortable={false} />
        <MaskField label="Pesel" source="pesel" sortable={false} />
        <TextField label="Bilet" source="ticketCode" sortable={false} />
        <TextField label="Wydarzenie" source="eventName" sortable={false} />
      </Datagrid>
    </>
  )
}

export const TicketInfoList = (props: ListProps) => {
  const { search } = useLocation()
  return (
    <>
      <FindUserByTicket />
      {!!search && (
        <List actions={false} pagination={false} {...props}>
          <TicketInfoGrid />
        </List>
      )}
    </>
  )
}
