import { ExtendedLog } from './extended-log'

const mapping: Partial<
  Record<keyof ExtendedLog, (value: string | number) => string>
> = {
  ticketCode: (value) => `l=>l.TicketCode=="${value}"`,
}

export const getFilterMapper = (key: keyof ExtendedLog) =>
  mapping[key] as (value: string | number) => string
