import * as React from 'react'
import {
  ChipFieldProps,
  DateField,
  Labeled,
  Show,
  ShowProps,
  Tab,
  TabbedShowLayout,
  TextField,
  useRecordContext,
} from 'react-admin'
import { Box, Chip, Divider } from '@material-ui/core'
import { TicketDetails } from '../../core/ticket-details/ticket-details'
import { mapFromBookingType } from '../../core/ticket-details/ticket-details-values.mapper'
import { TicketDetailsHeader } from './TicketDetailsHeader'
import { TicketDetailsTicketsField } from './TicketDetailsTicketsField'
import { BlockAccessButton } from './BlockAccessButton'
import { BlockTicketButton } from './BlockTicketButton'

const MappedChipField = <T extends unknown>({
  mapper,
  source,
}: ChipFieldProps & { mapper: (value: T) => string }) => {
  const record = useRecordContext<TicketDetails>()
  const mappedValue = mapper(record[source as string])
  return <Chip label={mappedValue} />
}

export const TicketDetailsView = (props: ShowProps) => (
  <>
    <Show {...props}>
      <>
        <TicketDetailsHeader />
        <Divider />
        <TabbedShowLayout syncWithLocation={false}>
          <Tab label="Bilet">
            <TextField label="Impreza" source="eventName" />
            <TextField label="Pesel" source="pesel" />
            <TextField label="Wariant" source="variantName" />
            <Labeled label="Rodzaj wejściówki">
              <MappedChipField
                source="bookingType"
                mapper={mapFromBookingType}
              />
            </Labeled>
            <DateField
              label="Aktywny od"
              source="activeFrom"
              emptyText="N/A"
              showTime
            />
            <DateField
              label="Aktywny do"
              source="activeTo"
              emptyText="N/A"
              showTime
            />
            <TicketDetailsTicketsField label="Wejściówki:" />
            <DateField label="Utworzono" source="createdAt" showTime />
            <DateField label="Zmodyfikowano" source="modifiedAt" showTime />
          </Tab>
        </TabbedShowLayout>
        <Divider />
        <Box p={1} display="flex" justifyContent="flex-end">
          <BlockAccessButton />
          <BlockTicketButton />
        </Box>
      </>
    </Show>
  </>
)
