import * as React from 'react'
import { useEffect, useState } from 'react'
import {
  sanitizeFetchType,
  UPDATE,
  UpdateParams,
  useMutation,
  useNotify,
  useRecordContext,
  useRefresh,
  useShowContext,
} from 'react-admin'
import { Button, createStyles, makeStyles, Theme } from '@material-ui/core'
import {
  Access,
  AccessToggleButtonStateEntry,
} from '../../core/ticket-details/access-button.state'
import { TicketDetails } from '../../core/ticket-details/ticket-details'
import { BookingUpdateRequest } from '../../core/bookings/booking-update.request'
import { BookingDisabledOption } from '../../core/enum/BookingDisabledOption'
import { ResourceName } from '../../core/ResourceName'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    button: {
      color: theme.palette.common.white,
      marginLeft: theme.spacing(1),
    },
  }),
)

type TicketButtonStateEntry = AccessToggleButtonStateEntry<
  BookingUpdateRequest['disabled']
>

const ticketButtonState: Record<Access, TicketButtonStateEntry> = {
  [Access.BLOCK]: [
    Access.BLOCK,
    {
      text: 'Zablokuj bilet',
      mutateParams: BookingDisabledOption.ADMIN,
    },
  ],
  [Access.UNBLOCK]: [
    Access.UNBLOCK,
    {
      text: 'Odblokuj bilet',
      mutateParams: null,
    },
  ],
  [Access.DISABLED]: [
    Access.DISABLED,
    {
      text: 'Zablokuj bilet (N/A)',
    },
  ],
}

export const BlockTicketButton = () => {
  const classes = useStyles()
  const { loading } = useShowContext()
  const notify = useNotify()
  const { id: bookingId, bookingDisabled } = useRecordContext<TicketDetails>()
  const [mutate, { loading: mutationLoading }] = useMutation()
  const [[access, buttonState], setButtonStateEntry] =
    useState<TicketButtonStateEntry>(ticketButtonState.DISABLED)
  const refresh = useRefresh()

  useEffect(() => {
    if (bookingDisabled === null) {
      setButtonStateEntry(ticketButtonState.BLOCK)
    } else if (bookingDisabled === BookingDisabledOption.ADMIN) {
      setButtonStateEntry(ticketButtonState.UNBLOCK)
    } else {
      setButtonStateEntry(ticketButtonState.DISABLED)
    }
  }, [bookingDisabled])

  const onClick = async () => {
    try {
      await mutate(
        {
          type: sanitizeFetchType(UPDATE),
          resource: ResourceName.BOOKINGS,
          payload: {
            data: { id: bookingId, disabled: buttonState.mutateParams },
          } as UpdateParams<BookingUpdateRequest>,
        },
        { returnPromise: true },
      )
      refresh()
    } catch (err) {
      notify(err?.message, 'error')
    }
  }

  return (
    <Button
      disabled={access === Access.DISABLED || loading || mutationLoading}
      className={classes.button}
      variant="contained"
      color="secondary"
      onClick={onClick}
    >
      {buttonState.text}
    </Button>
  )
}
