import * as React from 'react'
import {
  Datagrid,
  DateField,
  Filter,
  FilterProps,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
} from 'react-admin'

const LogGrid = () => (
  <Datagrid>
    <DateField label="Data" source="date" showTime />
    <TextField label="Urządzenie" source="deviceName" />
    <TextField label="Bilet" source="ticketCode" />
    <TextField label="Opis" source="text" sortable={false} />
    <TextField label="Rezerwacja" source="bookingId" />
  </Datagrid>
)

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput source="ticketCode" alwaysOn />
  </Filter>
)

export const LogList = (props: ListProps) => (
  <List
    perPage={20}
    exporter={false}
    pagination={<Pagination rowsPerPageOptions={[20, 50, 100]} />}
    filters={<ListFilters />}
    {...props}
  >
    <LogGrid />
  </List>
)
