import { TicketType } from '../enum/TicketType'
import { BookingType } from '../enum/BookingType'

const bookingTypeMapping: Record<BookingType, string> = {
  [BookingType.NORMAL]: 'Bilet pojedynczy',
  [BookingType.AB]: 'Abonament',
  [BookingType.INFIX]: 'Infix',
  [BookingType.POINTS]: 'Points',
  [BookingType.SERVICE]: 'Karta serwisowa',
}

const ticketTypeMapping: Record<TicketType, string> = {
  [TicketType.K]: 'Kod kreskowy',
  [TicketType.M]: 'Nr mifare',
  [TicketType.Z]: 'Nr dowodu',
  [TicketType.N]: 'Nr biletu',
  [TicketType.P]: 'Pesel',
  [TicketType.U]: 'Nr unique (125k)',
  [TicketType.R]: 'Nr rejestracyjny',
}

export const mapFromTicketType = (ticketType: TicketType): string =>
  ticketTypeMapping[ticketType]

export const mapFromBookingType = (bookingType: BookingType): string =>
  bookingTypeMapping[bookingType]
