import { DataProvider, GetListResult } from 'react-admin'
import { EXTENDED_LOGS_URL } from '../api-urls'
import { getWithPagination } from '../common/fetch.utils'
import { ExtendedLogDto } from '../dto/extended-log.dto'
import { ExtendedLog } from './extended-log'
import { LogListParams } from './log-list.params'
import { getFilterMapper } from './log-filter.mapper'
import { mapSortLogParam } from './log-sort.mapper'
import { ALL_RESULTS } from '../common/filter.values'

const getFilterParams = (filter: Partial<ExtendedLog>) => {
  if (Object.keys(filter).length) {
    const filterParams = Object.entries(filter).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof ExtendedLog)
      return mapper(value as string)
    })
    return filterParams.reduce((p, c) => `${p}||${c}`)
  }
  return ALL_RESULTS
}

const provider = {
  getList: async (
    resource: string,
    {
      filter,
      sort: { field, order },
      pagination: { page, perPage },
    }: LogListParams,
  ): Promise<GetListResult<ExtendedLog>> => {
    const filterParams = getFilterParams(filter)
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortLogParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage}`)

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const {
      data,
      range: { total },
    } = await getWithPagination<ExtendedLogDto[]>(EXTENDED_LOGS_URL, path)
    const extendedLogs = data.map(
      ({
        id,
        date,
        device: { name: deviceName },
        ticketCode,
        data: [{ text }],
        bookingId,
      }) =>
        ({
          id,
          date,
          deviceName,
          ticketCode,
          text,
          bookingId,
        } as ExtendedLog),
    )

    return Promise.resolve({
      data: extendedLogs,
      total,
    })
  },
} as DataProvider

export default provider
